/* eslint-disable */
import { useParams, useHistory } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { getUserByUsername } from '../services/firebase';
import * as ROUTES from '../constants/routes';
import Header from '../components/header';
import UserProfile from '../components/profile';
import OverLay from '../components/overlay';
import LoggedInUserContext from '../context/logged-in-user';
import useWindowDimensions from "../helpers/useWindowDimensions";

export default function Profile({loggedInUser}) {
  const { height, width } = useWindowDimensions();
  // console.log(loggedInUser)
  const { username } = useParams();
  const [user, setUser] = useState(null);
  // const [loggedInUser1, setLoggedInUser1] = useState(loggedInUser)
  const history = useHistory();
  // const { user, setActiveUser } = useUser(null);

  useEffect(() => {
    async function checkUserExists() {
      const [user] = await getUserByUsername(username);
      if (user?.userId) {
        setUser(user);
      } else {
        history.push(ROUTES.NOT_FOUND);
      }
    }

    checkUserExists();
  }, [username, history]);

  console.log(user)
  console.log(user?.userId)
  console.log(loggedInUser)
  console.log(loggedInUser?.uid === user?.userId)
  return user?.username ? (
    <LoggedInUserContext.Provider value={{ user, setUser }}>
      <div className="bg-gray-background">
        <Header />
        {/* {user?.userId === loggedInUser?.uid ? (<OverLay />) : null} */}
        <div className="mx-auto max-w-screen-lg">
          <UserProfile  user={user}/>
        </div>
      </div>
    </LoggedInUserContext.Provider>
  ) : null;
}
